@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@500&display=swap");
body,
html {
  font-family: "Montserrat", sans-serif;
  height: 100%;
  width: 100%;
  margin: 0;
  min-height: 100%;
}

.jumbotron {
  
  font-family: "Outfit", sans-serif;

  align-items: center;
  display: flex;
  
  flex-direction: column;

  width: 100vw;
  height: 100vh;
}

.jumbo-title {
  text-transform: capitalize;
  background-image: linear-gradient(
    -225deg,
    #231557 0%,
    #44107a 29%,
    #ff1361 67%,
    #ff9100 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 7s reverse infinite;
  display: inline-block;
  font-size: 60px;
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

.footer {
  padding: 50px 0 50px 0;

  width: 100vw;

  display: flex;
  justify-content: center;
}

.footer > ul {
  list-style: none;
}

.footer .social-icon {
  margin-right: 5px;
  vertical-align: middle;
  height: 48px;
  width: 48px;
}

.social-link {
  display: flex;

  margin-right: 50px;
  font-weight: bold;
  display: inline-block;
  align-items: center;
}

.circular-image {
    margin-top: -100px;
  border: 3px solid #ccc;
  width: 200px; /* Adjust the width and height as per your requirements */
  height: 200px;
  border-radius: 50%;
  object-fit: cover; /* Preserve the aspect ratio of the image */
  
}

.projects {
  padding: 50px 0 50px 0;

  width: 100vw;

  display: flex;
  justify-content: center;
}

.bio {
  height: 100vh;
  width: 100vw;
  justify-content: center;
}

.topImage{
    object-fit: cover;
    width: 100vw;
    display: inline-block ;
    height: 250px;
}

.tl {
    margin-top: 10px;
    height: 100vh;
    width: 100vw;
    /* border-radius: 100%/50px 9px 90px 9px; */
    background-image: linear-gradient(
    -225deg,
    #b3a7df 0%,
    #a089b9 29%,
    #db8ba6 67%,
    #ddc3a0 100%
  );
    }